<script>
var echarts = require('echarts')
export default {
  name: 'productdetailspage',
  data() {
    return {
      note: {
        borderStyle: 'none',
        width: '100%'
      },
      charts: '',
      tableData: [],
      indexData: {},
      chartData: [],
      timeData: [],
      acc: '',
      sorting: ''
    }
  },
  mounted() {
    this.acc = 'acc'
    this.sorting = '0'
    this.chart()
    this.getTableData()
  },
  methods: {
    tableSortChange(column) {
      console.log(column)
      this.acc = column.prop
      if (column.order === 'descending') {
        this.sorting = '0'
      } else {
        this.sorting = '1'
      }
      this.getTableData()
    },
    getTableData() {
      var url = this.COCKPIT.cockpitBaseURL + '/get_score/get_account_overview/' + this.acc + '/' + this.sorting
      this.$http
        .get(url)
        .then(
          response => {
            this.tableData = response.body.data
            this.indexData = response.body.data[0]
            this.getChartData(this.indexData.acc)
          },
          response => {
            console.log('连接出错Klinep')
          }
        )
        .catch(function(response) {
          console.log('连接catch-Kline' + response)
        })
    },
    getChartData(code) {
      var url = this.COCKPIT.cockpitBaseURL + '/get_score/get_chart/' + code
      this.$http
        .get(url)
        .then(
          response => {
            this.chartData = []
            this.timeData = []
            for (var i = 0; i < response.body.chart.length; i++) {
              this.chartData.push(response.body.chart[i][0])
              this.timeData.push(response.body.chart[i][1])
              this.chart()
            }
          },
          response => {
            console.log('连接出错Klinep')
          }
        )
        .catch(function(response) {
          console.log('连接catch-Kline' + response)
        })
    },
    tableRowStyle ({ row, rowIndex }) {
      return 'background-color: transparent'
    },
    tableHeaderColor ({ row, column, rowIndex, columnIndex }) {
      if (rowIndex === 0) {
        return 'background-color: transparent;color: #cdc4ec;'
      }
    },
    showDetail(row) {
      this.indexData = row
      this.getChartData(this.indexData.acc)
    },
    showDetail2(row) {
      this.$router.push({ name: 'cockpitDetails', params: { code: row.acc_info, acc: row.acc } })
    },
    tableCellStyle () {
      return 'border-color: #4E5D90;'
    },
    chart() {
      var option = {
        grid:
          {
            left: '120px',
            right: '60px',
            top: '10%',
            height: '85%'
          },
        xAxis: {
          type: 'category',
          data: this.timeData,
          axisLine: {
            lineStyle: {
              color: '#ffffff'
            }
          }
        },
        yAxis: {
          offset: '0',
          type: 'value',
          min: 0,
          max: 3000000,
          splitNumber: 7,
          axisLine: {
            lineStyle: {
              color: '#ffffff'
            }
          },
          splitLine: {
            lineStyle: {
              color: '#36394F',
              type: 'dashed'
            }
          }
        },
        series: [{
          data: this.chartData,
          type: 'line',
          lineStyle: {
            color: '#b26dea'
          },
          areaStyle: {
            color: {
              type: 'linear',
              x: 0,
              y: 0,
              x2: 0,
              y2: 1,
              colorStops: [{
                offset: 0, color: '#494C8E'
              }, {
                offset: 1, color: '#111111'
              }],
              global: false
            }
          }
        }]
      }
      if (this.charts) {
      } else {
        this.charts = echarts.init(this.$refs.chart)
      }
      this.charts.clear()
      this.charts.setOption(option)
    }
  }
}
</script>

